import {CubicBezierSegment, InvoluteGear} from "./InvoluteGear";
import {Shape} from "./Shape";
import {Matrix3x3} from "@mkrabset/krabzcnc-view2d/dist/cjs/2d/Matrix3x3";
import {Vector2d} from "@mkrabset/krabzcnc-view2d/dist/esm/2d/Vector2d";
import {BooleanShapeOperation} from "./BooleanShapeOperation";
import {Line} from "./Line";


export class RingGear {
    private numberOfTeeth:number;
    private pinion: InvoluteGear;
    private backlash: number;
    private pitchRadius: number;

    constructor(pinion:InvoluteGear, numberOfTeeth:number, backlash: number) {
        this.numberOfTeeth = numberOfTeeth;
        this.pinion = pinion;
        this.backlash = backlash;

        const pinionPitchRadius = this.pinion.getDimensions().pitchRadius
        const zP= this.pinion.getDimensions().numberOfTeeth
        this.pitchRadius = pinionPitchRadius/zP * this.numberOfTeeth
    }

    calculateFlank() {
        const pinionPitchRadius = this.pinion.getDimensions().pitchRadius
        const toothProfile=this.pinion.generateToothProfile()

        const simplifiedToothProfile = Line.rdp(toothProfile, 0.005)

        console.log({toothProfile, simplifiedToothProfile})


        const toothShape=new Shape(Shape.pointsToLines(simplifiedToothProfile)).oriented(true)
        const liftTooth=Matrix3x3.translate(new Vector2d(0,this.pitchRadius-pinionPitchRadius))

        const limit = pinionPitchRadius * Math.PI*2 / this.numberOfTeeth * 15
        const shapes = []
        for (let d= -limit; d<limit;d+=0.4) {
            const rotated=toothShape.transform(Matrix3x3.rotate(d/pinionPitchRadius))
            const shifted = rotated.transform(liftTooth)
            const rolledBack = shifted.transform(Matrix3x3.rotate(-d/this.pitchRadius))
            shapes.push(rolledBack)
        }

        const cutPiece = shapes.reduce((a, b) =>BooleanShapeOperation.combine(a,b));
        const pointsWithoutBacklash = Shape.linesToPoints(cutPiece.segs)
            .filter(point=>point.length()> this.pitchRadius-this.pinion.getDedendum())
            .filter(point=>point.x>=0)
            .reverse()

        const points = this.applyBacklash(pointsWithoutBacklash)

        // Extends to center at the bottom
        const ep=points[points.length - 1]
        const sp = points[0]
        const firstAngle = Math.PI / 2 - Math.atan(sp.x / sp.y)
        const prefix: Vector2d[] = []
        for (let a = Math.PI / 2; a > firstAngle; a -= 0.001) {
            prefix.push(new Vector2d(Math.cos(a), Math.sin(a)).multiply(this.pitchRadius+this.pinion.getAddendum()))
        }

        // Extend to center at top (if neccessary)

        const lastAngle=Math.PI / 2 - Math.atan(ep.x / ep.y)
        const suffix:Vector2d[] = []
        for (let a = lastAngle-0.001; a > Math.PI / 2 - Math.PI*2/this.numberOfTeeth/2; a -= 0.001) {
            suffix.push(new Vector2d(Math.cos(a), Math.sin(a)).multiply(this.pitchRadius-this.pinion.getDedendum()))
        }
        return [...prefix, ...points, ...suffix];
    }

    calculateTooth() {
        const flank=this.calculateFlank()
        const flip=Matrix3x3.scale(-1,1)
        const revFlank=flank.map(p=>flip.transform(p)).reverse()
        return [...revFlank, ...flank]
    }

    calculateGear(): CubicBezierSegment[] {
        const tooth=this.calculateTooth()
        const toothCurve= InvoluteGear.points2Curve(tooth)

        const result = []
        for (let i=0;i<this.numberOfTeeth;i++) {
            const rot = Matrix3x3.rotate(-Math.PI*2/this.numberOfTeeth*i)
            result.push(...toothCurve.map(seg=>InvoluteGear.transformSeg(seg, rot)))
        }
        return result;
    }

    applyBacklash(curve:Vector2d[]): Vector2d[] {
        if (this.backlash>0) {
            const d=this.pitchRadius
            const backlashAngle=(-this.backlash/d/2)
            const rot=Matrix3x3.rotate(backlashAngle)
            return curve.map(p=>rot.transform(p))
        } else {
            return curve
        }
    }
}