import {Line} from "./Line";
import {Matrix3x3} from "@mkrabset/krabzcnc-view2d/dist/cjs/2d/Matrix3x3";
import {Vector2d} from "@mkrabset/krabzcnc-view2d/dist/esm/2d/Vector2d";


export class Shape {
    public readonly segs: Line[];
    private area: number | null = null;

    constructor(segs: Line[]) {
        this.segs = segs;
    }

    public getArea(): number {
        if (this.area === null) {
            this.area = this.segs.map(seg => seg.start.cross(seg.end)).reduce((a, b) => a + b) / 2;
        }
        return this.area
    }

    public transform(matrix: Matrix3x3): Shape {
        return new Shape(this.segs.map(seg => seg.transform(matrix)))
    }

    public reverse(): Shape {
        return new Shape(this.segs.map(seg => new Line(seg.end, seg.start)).reverse())
    }

    public oriented(ccw: boolean): Shape {
        return ccw === (this.getArea() > 0) ? this : this.reverse()
    }

    static pointsToLines(points: Vector2d[]): Line[] {
        return points.map((p, ind, arr) => new Line(p, arr[(ind + 1) % arr.length]))
    }

    static linesToPoints(lines: Line[]) {
        return [lines[0].start, ...lines.map(line=>line.end)]
    }


    static simplifyPath(point: Vector2d[], error: number) {

    }

}